import React from 'react';

class SearchBar extends React.Component {

  state = {term: ''};


 //  event is a JS object that contains info about the event that just occurred
//  onInputChange(event){
    //this will contain the text that the user just added to our input
//    console.log(event.target.value);
//  };

onFormSubmit = event =>  {
  event.preventDefault();

    this.props.onSubmit(this.state.term);
};


  render() {
    return (
    <div className="ui segment">
      <form onSubmit={this.onFormSubmit} className="ui form">
        <div className="field">
          <label>Image Search</label>
          {/*  we don't put parenthesis on the function callback otherwise it would be called every time the component is rendered.
          We only want to call that function when the input changes */}
            <input
              type="text"
              value={this.state.term}
              onChange={e => this.setState({term : e.target.value})}
            />
          {/*  <input type="text" onChange={this.onInputChange} />
            Abbreviated styntax for the upper 'onChange' :
              onChange={(event) => console.log(event.target.value)}
              In which case we would not need the onInputChange function anympre!
            */}
        </div>
      </form>
    </div>
  );
  }
}

export default SearchBar;
